import { Component, OnInit } from '@angular/core';
import {Subject} from 'rxjs';
import {Observable, interval, timer} from 'rxjs';
import {takeUntil} from 'rxjs/operators';
import {WebcamImage, WebcamInitError, WebcamUtil} from 'ngx-webcam';
import {AnalyzePhotoService} from '../analyze-photo.service';
import {Router} from '@angular/router';
import Typed from 'typed.js';

@Component({
  selector: 'app-experience',
  templateUrl: './experience.component.html',
  styleUrls: ['./experience.component.css']
})
export class ExperienceComponent implements OnInit {

  private trigger: Subject<void> = new Subject<void>();
  public webcamImage: WebcamImage = null;
  public showWebcam = true;
  public loading:boolean = false;
  public counter:number = 3;
  public intervalObject:any;
  public showCountdown = false;

  
  constructor(public aps:AnalyzePhotoService, private router:Router) { 
    aps.results = {};

  }

  ngOnInit() {
  }

  hack(val) {
    return Array.from(val);
  }

  public handleTakePictureButton(){
    this.startCountdown();
  }

  public triggerSnapshot(): void {
    
    this.loading = true;
    this.trigger.next();
    this.toggleWebcam();

    this.aps.doPOST(this.webcamImage.imageAsBase64).then( ()=>{
      console.log('got some data boooooy');
      console.log(this.aps.results);
      this.loading = false;

      // show analyzing screen
      this.getPersonality();
    });
    
  }

  public getPersonality(){
    var drink = "Beer";
    var res = this.aps.results;
    var sex = res[0].Value;
    var age = res[1].Low;
    var smile = false;
    if(res[2].Value == true && res[2].Confidence > 70){
      smile = true;
    }
    var emotion = res[3][0].Type + "("+Math.round(res[3][0].Confidence)+"%)";
    var facialHair = false;
    if(res[4].Value == true && res[4].Confidence > 70){
      facialHair = true;
    }
    var eyeglasses = false;
    if(res[5].Value == true && res[5].Confidence > 70){
      eyeglasses = true;
    }

    if(sex == "Male"){
      // male track
      drink = "Old Fashioned";
      if(smile){
        if(eyeglasses){
          drink = "The Prototype";
        }
        if(facialHair){
          drink = "Machine Learning";
        }
      }else{
        drink = "Mindset";
      }
    }else{
      // female track
      drink = "Cosmo";
      if(smile){
        if(eyeglasses){
          drink = "The Journey";
        }else{
          drink = "Disruption";
        }
        
      }else{
        if(eyeglasses){
          drink = "Innovation";
        }else{
          drink = "Mindset";
        }
        
      }
    }
    console.log("Sex = "+sex+" | age: "+age+" | smile: "+smile+" | emotion: "+emotion+" | facial hair: "+facialHair+" | glasses: "+eyeglasses);
    console.log("DRINK = "+drink);

    // show the info
    var options = {
      strings: ["Sex: "+sex, "Estimated age: "+age, "Smiling: "+smile, "Emotion: "+emotion,"Beard / Mustache: "+facialHair, "Glasses: "+eyeglasses,"Mapping data to drink menu...",".......... .......... ........... .........." ],
      typeSpeed: 30
    }
    
    var typed = new Typed(".element", options);
    setTimeout(() => {
      this.router.navigate(['thank-you', drink]); 
    }, 15000);
    //
  }

  

  public startCountdown(){

   this.showCountdown = true;
    var source = interval(1000);
    //after 5 seconds, emit value
    var timer$ = timer(4000);
    //when timer emits after 5s, complete source
    const example = source.pipe(takeUntil(timer$));
    //output: 0,1,2,3
    const subscribe = example.subscribe(val => {
      this.counter--;
      console.log("counting down");
        if(this.counter <= 0){
       subscribe.unsubscribe();
        this.triggerSnapshot();
        this.counter = 3;
        this.showCountdown =false;

        }
    });
 
  }

  
  public triggerReset(): void {
    this.webcamImage = null;
    this.aps.results = {};
    this.showWebcam = true;
  }

  public toggleWebcam(): void {
    this.showWebcam = !this.showWebcam;
  }

  public handleImage(webcamImage: WebcamImage): void {
    console.info('received webcam image', webcamImage);
    this.webcamImage = webcamImage;
    //console.log(webcamImage.imageAsBase64);
  }

  public get triggerObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

}
